@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@200&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.header{
    height: 50px;
    padding: 20px;
    margin: 0px;
    width: 100%;
    background-color: rgb(67, 8, 177);
    color: white;
    font-family: 'Montserrat', sans-serif;
    text-align:start;
    font-size: 30px;
}
.input{
    border: 2px solid rgb(67, 8, 177);
    border-radius: 2px;
}
.remove{
    cursor: pointer;
    background-color: rgb(67, 8, 177);
}
.search-content{
    font-size: 18px; 
    text-align: start;
    margin-left: 50px;
    margin-top: 20px;
    margin-bottom: 20px;
}
.pic{
    height: 175px;
    width: 205px;
}
.box {
    height: 150px;
    width: 600px;
    box-shadow: 0 0 2px white;
    border: 1px solid white;
    margin: 50px;
  }
  .media {
    display: flex;
    border: 2px solid rgb(67, 8, 177);
    flex-direction: row;
    justify-content: space-evenly;
    align-items: center;
    background-color: #ced6ce;
    padding: 10px 20px;
  }
  .field {
    display: flex;
    flex-direction: row;
  }
  
  .is-info {
    margin-left: 2px;
  }
  
  .pic {
    width: 120px;
    height: 100px;
  }
  .content p {
    margin-left: 10px;
  }
  .field {
    display: flex;
    flex-direction: row;
  }
  
  .button {
    background-color: rgb(92, 92, 199);
    width: 30px;
    
    cursor: pointer;
  }
  #searchbox{
      height: 30px;
      width: 250px;
  }
  .main {
    display: flex;
  }
  
  .list {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    width: 250px;
  }
  .cal b {
    font-size: 25px;
  }
  
  .list div {
    font-size: 20px;
  }
  
  .list > button {
    width: 30px;
    color: white;
    font-size: 20px;
    background-color: rgb(67, 8, 177);
    border: none;
  }
  
  .list > button:hover {
    -webkit-transform: scale(1.2);
            transform: scale(1.2);
    outline-color: black;
  }
  p{
      padding: 10px;
  }
.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

